.slider {
    position: relative;
    margin-bottom: 7vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .slider-image {
    max-width: 1000px;
    width: 100%;
    max-height: 600px;
    height: auto;
    border-radius: 4px;
  }
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: #000;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: #000;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
  }

 .copyright {
  display: flex;
  justify-content: center;
  color: #444343;
  font-weight: 100;
  font-size: calc(5px + 6 * ((100vw - 320px) / 680));
  margin: 35px;
  margin-top: 40px;
  margin-bottom: 20px;
 }